.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.cell {
    width: 30px;
    height: 30px;
    border: 1px solid #AEC1FF;
    border-radius: 2px;    
    margin: 3px;
    display: inline-block;
}

.board-row {
    font-size: 0px;
    letter-spacing: 0px;
    word-spacing: 0px;
}

.App-link {
  color: #61dafb;
}

.historyContainer {
    margin: 0px 100px;
    border-right: 1px solid rgba(0, 0, 0, 0.233);
    height: 100%;
    pointer-events: none;
}

.historyContainer h2 {
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: darkcyan;
  padding-right: 90px;
  height: 30px;
}

.historyContainer .board {
    margin: 15px;
    padding: 15px;
    display: inline-block;
    border: 1px solidr rgba(0, 0, 0, 0.233);
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.historyContainer .cell {
    width: 15px;
    height: 15px;
    border: 1px solid black;
    border-radius: 2px;    
    margin: 1px;
    display: inline-block;
}

.main-board .cell {
  box-shadow: 0 1px 2px rgba(0,0,0,0.5);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
